.promo {
  background-color: @color-promo;
  background-image: url("../img/hero-mobile@2x.jpg");
  background-repeat: no-repeat;
  background-position: 50% 0;
}

.promo__title {
  padding: 73px 30px 120px;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  text-align: center;
  color: @color-black;
}

.promo__goods {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

@media (min-width: @tablet-width) {
  .promo {
    position: relative;
    background-image: url("../img/hero-desktop.jpg");
    background-repeat: no-repeat;
    background-position: 50% 0;
  }

  .promo::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 24px;
    left: 0;
    bottom: 0;
    background-image: url("../img/zigzag-fill-tablet.svg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: 0 0;
  }

  .promo__title {
    padding: 154px 50px 372px;
    font-size: 43px;
    line-height: 50px;
  }

  .promo__goods {
    flex-direction: row;
  }
}

@media (min-width: @desktop-width) {
  .promo__title {
    padding: 158px 50px 355px;
    font-size: 53px;
    line-height: 57px;
  }

  .promo {
    background-image: url("../img/hero-desktop.jpg");
    background-repeat: no-repeat;
    background-position: 50% 0;
    object-fit: contain;
  }

  .promo::after {
    height: 34px;
    background-image: url("../img/zigzag-fill-desktop.svg");
    background-repeat: repeat-x;
    background-size: auto 154%;
    background-position: center top;
  }
}
